						<p> <span>问题:</span> <span>{{nowquest.DisplayOder}}</span></p>
						<p> <span>题型:</span> <span>{{nowquest.EvaluationAngle}}</span></p>
						<p><span>分数:</span> <span>{{nowquest.ChoiceCount}} 分</span> </p>
					</div>
					<p class="questtext">
						{{nowquest.QuestionContent}}
					</p>

