						<div>
							<ul class="resList">
								<li v-for="(item,index) in items">
									<div>
										<p> {{ index+1 }}</p>
										<div v-bind:class="flag[index] ? classA:classB">
											{{ item.num }}
										</div>
									</div>

								</li>
							</ul>
						</div>
					</div>
				</div>
			</div>
			<div class="bottom-button">
