		padding-top: 20px;
	}
	
	.message p {
		margin-bottom: 7px;
		text-align: left;
	}
	
	.message p span:nth-of-type(1) {
		display: inline-block;
		width: 20%;
		box-sizing: border-box;
		margin-left: 5%;
	}
	
	.message p span:nth-of-type(2) {
		display: inline-block;
		width: 70%;
		box-sizing: border-box;
		text-align: left;
	}
	
	.contentBox h3,
	.contentBox h4 {
		text-align: left;
		position: relative;
		left: 3%;
	}
	
	.contentBox:nth-of-type(2) {
		width: 70%;
		height: 100%;
		box-sizing: border-box;
	}
	
	.contentBox_1 {
		border: 2px solid #fff;
		width: 95%;
		margin: auto;
		height: 80%;
	}
	
	.questinfo {
		padding-top: 10px;
	}
	
	.questinfo::after {
		content: "";
		display: block;
		clear: both;
	}
	
	.questinfo p {
		display: inline-block;
		width: 30%;
		text-align: left;
		font-size: 23px;
	}
	
	.questinfo p:nth-of-type(1) {
		width: 30%;
	}
	
	.questtext {
		color: rgba(81, 81, 253, 0.795);
		font-size: 23px;
		width: 80%;
		margin: auto;
		padding: 10px 5px;
		height: 30%;
	}
	
	.contentBox_2 {
		margin-bottom: 10px;
	}
	
	.contentBox_2 h4 {
		margin-bottom: 10px;
	}
	
	.contentBox_2>div {
		width: 90%;
		margin: auto;
		border: 2px solid #fff;
		padding: 20px 40px;
		text-align: left;
	}
	
	.contentBox_2>div button {}
	
	.resList {
		height: 40px;
		margin-bottom: 20px;
	}
	
	.resList li>div {
		text-align: center;
	}
	.dia_ques {
		position: absolute;
		left: 725px;
		top: 250px;
		z-index: 100;
	}
	
	.bottom-button {
		top: 81%;
		left: 5%;
	}
</style>


