import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import index from '@/components/index'
import textcontent from '@/components/textContent'
import choicePeople from '@/components/choicePeople'
import choiceDepartment from '@/components/choiceDepartment'
import conform from '@/components/conform'
import makeAnswer from '@/components/makeAnswer'

import 'vue-easytable/libs/themes-base/index.css'
// 导入 table 和 分页组件
import { VTable, VPagination } from 'vue-easytable'

// 注册到全局
Vue.component(VTable.name, VTable)
Vue.component(VPagination.name, VPagination)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }, {
      path: '/index',
      name: 'index',
      component: index
    }, {
      path: '/textcontent',
      name: 'textcontent',
      component: textcontent

    }, {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld

    }, {
      path: '/choicePeople',
      name: 'choicePeople',
      component: choicePeople

    }, {
      path: '/choiceDepartment',
      name: 'choiceDepartment',
      component: choiceDepartment

    }
    , {
      path: '/conform',
      name: 'conform',
      component: conform

    }
    , {
      path: '/makeAnswer',
      name: 'makeAnswer',
      component: makeAnswer

    }
  ]
})



