<template>
<div class="myBody">
    <div class="bg">
        <img
            src="../assets/bg_2.jpg"
            alt=""
        ></div>
    <div class="content">
      <div class="title">
        <h2>支   部   选    择</h2>
        <br>
      </div>
      <div class="depatementTable">
          <v-table
           is-vertical-resize
           
             is-horizontal-resize
            style="width:100%;height:100%;text-align:center"
               
             :columns="columns"
             :table-data="tableData"
            :row-click="choose"
             :show-vertical-border="true"
               row-hover-color="#eee"
            row-click-color="#edf7ff"
    ></v-table>
      </div>
      
        <div class="bottom-button">
            <div>
                <button
                @click="next('choicePeople')"
                  
                    class="button-1"
                >确定</button>
            </div>
            <div>
                <router-link
                    to="/"
                    class="button-1"
                >返回</router-link>
            </div>

        </div>

    </div>

</div>
</template>


<script>
export default {
  name: "choiceDepartment",
  methods: {
    choose(rowIndex, rowData, column) {
//    alert("选择了 " + rowData.PartyBranchName);
      this.chooseId = rowData.PartyBranchID;
    },
    next(url) {
      if (this.chooseId != -1) {
        this.$router.push({
          name: url,
          params: {
            PartyBranchID: this.chooseId
          }
        });
      } else {
      	alert("请先选择支部");
      }
    },
    listUpdate(list) {
      this.tableData = list;
    },
    closeDoor() {}
  },
  created: function() {
    this.$ajax({
      method: "get",
      url: "http://202.206.249.16:8080/dxpj/zp/get_party_selected"
    })
      .then(res => {
        //info

        res = res.data;

        if (res.status.code == 100) {
          var list = res.info;
        } else if (res.status.code == 103) {
          var list = [
            {
              PartyBranchID: "-1",
              PartyBranchName: "暂无",
              City: "暂无"
            }
          ];
        }

        this.listUpdate(list);
      })
      .catch(function(err) {
        console.log(err);
      });
  },
data() {
    return {
      chooseId: -1,
      tableData: [
        {
          PartyBranchID: "1",
          PartyBranchName: "test1",
          City: "北京"
        },
        {
          PartyBranchID: "2",
          PartyBranchName: "test1",
          City: "秦皇岛"
        },
        {
          PartyBranchID: "3",
          PartyBranchName: "test1",
          City: "河北"
        },
        {
          PartyBranchID: "4",
          PartyBranchName: "test1",
          City: "安徽"
        },
        {
          PartyBranchID: "5",
          PartyBranchName: "test1",
          City: "上海"
        }
      ],
      columns: [
        {
          field: "PartyBranchID",
          title: "支部编号",
          width: 100,
          titleAlign: "left",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "PartyBranchName",
          title: "支部名称",
          width: 600,
          titleAlign: "left",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "City",
          title: "支部所在省",
          width: 200,
          titleAlign: "left",
          columnAlign: "center",
          isResize: true
        }
      ]
    };
}
};
</script>

<style scoped>
.bottom-button {
  top: 75% !important;
}
.depatementTable {
  margin: auto;
  width: 70%;
  height: 60%;
}
</style>



