<template>
	<div class="dialog">
		<div class="dialog_mask"></div>
		<div class="dialog_container">
			<div class="dialog_content">
				<div class="dialog_content_top">{{messageOne}}</div>
				<div class="dialog_btn">
					<a href="javascript:;" class="btn" @click="determine">确定</a>					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	//	import redLineDia from './redLineDia'
	export default {

		props: ['messageOne'],
		methods: {
			determine(){
				this.$emit('determine',false);
			}
		},
				
		
		data() {
			return {
			};
		}

	};
</script>

<style>
	.dialog {}
	
	.dialog_mask {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.dialog_container {
		background: #fff;
		width: 450px;
		height: 120px;
		position: relative;
		border-radius: 10px;
		margin: 0 auto;
		background: #fef189;

	}
	
	.dialog_content {
		text-align: center;
		padding-top: 30px;
	}
	
	.dialog_btn {
		margin-top: 20px;
		line-height: 30px;
	}
	
	.dialog_btn a {
		background: #f9390b;
		padding: 4px 30px;
		border-radius: 5px;
		color: #fff;
		text-decoration: none;
		width: 50px;
		
		
	}
	
	.dialog_btn a:nth-child(2) {
		margin-left: 20px;
	}
</style>


