<template>
	<div class="myBody">
		<div class="bg"><img src="../assets/bg_2.jpg" alt=""></div>
		<div class="content">
			<div class="text">
				<p>
					{{tableData.info_AssesingTypeName}}
				</p>
			</div>
			<div class="bottom-button">
				<div>
					<router-link to="/choiceDepartment" class="button-1">跳过</router-link>
				</div>

			</div>
		</div>

	</div>
</template>

<script>
	export default {
		name: "textcontent",
		data() {
			return {
				msg: "test",
				tableData: {
					status: {
						msg: "操作成功",
						code: 100,
						status: 200
					},
					info_AssesingTypeName: "....."

				}
			};
		},
		methods: {
			listUpdate(list) {
				this.tableData = list;
			}
		},
		created: function() {
			//  var id = this.$route.params.PartyBranchID;//接受参数PartyBranchID  https://www.cnblogs.com/lulianlian/p/7682790.html

			this.$ajax({
					method: "get",
					url: "http://202.206.249.16:8080/dxpj/zp/get_generalDescription"
				})
				.then(res => {
					//info

					res = res.data;

					if(res.status.code == 100) {
						var list = res;
					} else if(res.status.code == 111) {
						alert(res.status.msg);
					}

					this.listUpdate(list);
				})
				.catch(function(err) {
					console.log(err);
				});
		}
	};
</script>

<style scoped>
	.button-1 {
		width: 20%;
		margin: auto;
	}
	
	.text {
		position: relative;
		width: 60%;
		margin: auto;
		text-align: center;
		top: 8%;
		color: #000;
		font-size: 30px;
	}
	
	.text p {
		margin-bottom: 30px;
	}
	
	.bottom-button>div {
		width: 80%;
		display: inline-block;
		box-sizing: border-box;
		text-align: center;
	}
</style>


