<template>
  <div class="myBody">
    <div class="bg"><img src="../assets/bg_1.jpg" alt=""></div>
    <div class="content">
    	<div class="contentTitle">
				<p class="titleText">{{tableData.info_A_description}}</p>
				<p class="titleText_1">{{tableData.info_AssesingTypeName}}</p>
			</div>
      <div class="bottom-button">
      <div>
       <button  @click="next" class="button-1">进入体检</button >
        </div>
    <div> 
        <button @click="exit" class="button-1">退出</button>
    </div>
     
      </div>
 
    </div>
 
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      msg: "test",
      tableData: {
					status: {
						msg: "操作成功",
						code: 100,
						status: 200
					},
					info_AssesingTypeName: "党性互评端",
					info_A_description: "渭阳西路街道党员党性教育体检中心"
				}
    };
  },
  methods: {
    exit: function() {
      if (confirm("确认退出?")) {
        window.close();
      }
    },
    next(){
       this.$ajax({
          method: "get",
          url:
            "http://202.206.249.16:8080/dxpj/zp/judge_IsActivity"
           
        })
          .then(res => {
            //info

            res = res.data;

            if (res.code == 100) {
             
              this.$router.push({
                name: 'textcontent',
              });
            } else if (res.code == 102) {
             alert(res.msg);
            } 
          })
          .catch(function(err) {
            console.log(err);
          });
    },
    listUpdate(list) {
				this.tableData = list;
			}
		},
		created: function() {
			//  var id = this.$route.params.PartyBranchID;//接受参数PartyBranchID  https://www.cnblogs.com/lulianlian/p/7682790.html

			this.$ajax({
					method: "get",
					url: "http://202.206.249.16:8080/dxpj/zp/get_A_Description"
				})
				.then(res => {
					//info

					res = res.data;

					if(res.status.code == 100) {
						var list = res;
					} else if(res.status.code == 111) {
						alert(res.status.msg);
					}

					this.listUpdate(list);
				})
				.catch(function(err) {
					console.log(err);
				});
		}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

.bottom-button > div {
  width: 49%;
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
}
.bottom-button .button-1 {
  max-width: 50%;
  position: relative;
}
.titleText{
		color:rgb(255, 230, 0); 
		margin:60px; 
        font-size: 45px;
        font-weight: bold;
	}
	.titleText_1{
		color:rgb(255, 230, 0); 
		margin:80px; 
        font-size: 25px;
           
	}
</style>



